
export const announcementsUtils = (timeStamp, showAt, endAt, every, anncKey) => {
       try {
              const timeInterval = getTimeFromDate(getConvertToDate(showAt)) / 1000;
              const aEndTime = getTimeFromDate(getConvertToDate(endAt)) / 1000;
              const repeatOnce = every !== 0
                     ? (timeStamp - timeInterval + anncKey * 10) % (every * 60)
                     : timeStamp - timeInterval - 23400 + anncKey * 10;
              return { repeatOnce, timeInterval, aEndTime };
       } catch {
              return { repeatOnce: 0, timeInterval: 0, aEndTime: 0 };
       }
};

export const getConvertToDate = (date) => {
       try {
              if (navigator.userAgent.indexOf("Safari") !== -1) {
                     let dateData = new Date(date);
                     let dateParts = dateData.split(/[- :T]/);
                     let [year, month, day, hour, minute, second] = dateParts.map(Number);
                     return new Date(year, month - 1, day, hour, minute, second);
              } else {
                     return new Date(date);
              }
       }
       catch (ex) {
              return new Date(date);
       }
}


export const getTimeFromDate = (date) => date.getTime();
export const setIntervalFunction = (intervalFunction, duration) => setInterval(intervalFunction, duration);
export const isFullScreen = () => isBrowser() || false;
export const isIframe = window.self !== window.top;

const isBrowser = () => typeof window !== 'undefined' && typeof document !== 'undefined';
export const isStudio = window.location.href.includes('studio=1')


